import { Alert, Accordion, Form, Button, Container, Row, Col, Badge, Stack } from 'react-bootstrap';
import { useState } from 'react';
import { SINGING_LESSON, GUITAR_LESSON, DRUM_LESSON, PIANO_LESSON, ORCHESTRAL_LESSON, PRODUCTION_LESSON, ROAD_CREW_LESSON } from '../constants.js';
import NFTCardList from '../components/NFTCardList';
import {loadInstrumentCards, parseStats, login} from '../utils/RisingStarUtils';

const DEFAULT_LUCKS_STATE = {
  totalCartas: 0,
  canto: 0,
  cartasCanto: [],
  cartasGuitarra: [],
  cartasOrquesta: [],
  cartasProduccion: [],
  cartasPiano: [],
  cartasDrum: [],
  cartasRoadCrew: [],
  guitarra: 0,
  bateria: 0,
  piano: 0,
  orquesta: 0,
  produccion: 0,
  carretera: 0,
};

function LuckInstruments() {
  const [ cargando, setCargando ] = useState(false);
  const [ username, setUsername ] = useState("");
  const [ lucks, setLucks ] = useState(Object.assign({}, DEFAULT_LUCKS_STATE));
  // const [ error, setError ] = useState(null);
  const [ notIncludedCards, setNotIncludedCards ] = useState([]);

  const changeUsername = (e) => {
    setUsername(e.target.value);
  }

  const addCartas = (cartas, card) => {
    let inc = cartas.filter(carta => carta.data.properties.type === card.properties.type);
    if (inc.length === 0) {
      cartas.push({data:card, cant:1});
    } else {
      const mismaCarta = cartas.filter(carta => carta.data.properties.type === card.properties.type);
      if (mismaCarta !== null) mismaCarta[0].cant++;
    }
  }

  const parseData = (data, res) => {
    // console.log("*** res= ", res);
    res.totalCartas += data.length;

    for (let i = 0; i < data.length; ++i){
      const card = data[i];
      if (card.properties.class === "instrument" || card.properties.class === "transport") {
        const type = card.properties.type;
        let number = type.indexOf(' ')>0 ? type.substring(0, type.indexOf(' ')).toLowerCase() : type.toLowerCase();
        let stats = parseStats(card.properties.stats);

        if (SINGING_LESSON.includes(number)) {
          addCartas(res.cartasCanto, card);
          res.canto += parseInt(stats.luck);
        }
        else if (GUITAR_LESSON.includes(number)) {
          addCartas(res.cartasGuitarra, card);
          res.guitarra += parseInt(stats.luck);
        }
        else if (DRUM_LESSON.includes(number)) {
          addCartas(res.cartasDrum, card);
          res.bateria += parseInt(stats.luck);
        }
        else if (PIANO_LESSON.includes(number)) {
          addCartas(res.cartasPiano, card);
          res.piano += parseInt(stats.luck);
        }
        else if (ORCHESTRAL_LESSON.includes(number)) {
          addCartas(res.cartasOrquesta, card);
          res.orquesta += parseInt(stats.luck);
        }
        else if (PRODUCTION_LESSON.includes(number)) {
          addCartas(res.cartasProduccion, card);
          res.produccion += parseInt(stats.luck);
        }
        else if (ROAD_CREW_LESSON.includes(number)) {
          addCartas(res.cartasRoadCrew, card);
          res.carretera += parseInt(stats.luck);
        }
        else {
          console.log(number + ' - ' + type + ' - NO CONTEMPLADO EN NINGUNA LECCION. ', card);
          setNotIncludedCards(oldArray => {
            if (!oldArray.includes(type)) {
              return [...oldArray, type];
            }
            return oldArray;
          });
        }
      } else if (card.properties.class === "crafted") {
          const type = card.properties.type;
          let stats = parseStats(card.properties.stats);
          if (type.match('RSTAR G')) {
              addCartas(res.cartasGuitarra, card);
              res.guitarra += parseInt(stats.luck);
          } else if (type.match('RSTAR P')) {
            addCartas(res.cartasProduccion, card);
            res.produccion += parseInt(stats.luck);
          } else if (type.match('RSTAR D')) {
            addCartas(res.cartasDrum, card);
            res.bateria += parseInt(stats.luck);
          }
        }
    }
  }

  const doCalculate = async () => {
    const cards = await loadInstrumentCards(username);

    let lucks = Object.assign({}, DEFAULT_LUCKS_STATE);
    setNotIncludedCards([]);
    lucks.cartasCanto = [];
    lucks.cartasGuitarra = [];
    lucks.cartasOrquesta = [];
    lucks.cartasProduccion = [];
    lucks.cartasPiano = [];
    lucks.cartasDrum = [];
    lucks.cartasRoadCrew = [];

    parseData(cards, lucks);
    setLucks(lucks);
  }

  const calculate = async () => {
    setCargando(true);
    // TODO: Mantener el login
    login(username, async (res) => {
      if (res.success) {
        await doCalculate();
      } else { // ELSE, SOMETHING FAILED UPON LOGIN, PRINT ERROR TO CONSOLE
        console.error(res.message);
        alert("Login error " + res.message)
      }

      setCargando(false);
    });
  }

  let renderLucks = (<div>Complete username and click Calculate</div>);

  const renderItem = (index, total, cartas, label) => {
    // console.log("renderItem::cartas=", cartas);
    // console.log("renderItem::total=", total);
    // const _total = cartas.reduce((total, card) => {
    //   let _stats = parseStats(card.data.properties.stats);
    //   total += card.cant * parseInt(stats.luck);
    //   return total;
    // }, 0);
    // console.log("renderItem::_total=", _total);

    return (<Accordion.Item eventKey={index}>
        <Accordion.Header><span>{label}</span> <Badge bg="secondary" style={{marginLeft: '10px'}} role={(label+' total luck').replaceAll(' ', '-')}>{total}</Badge></Accordion.Header>
      <Accordion.Body style={{ display: 'flex', flexWrap: 'wrap' }}>
        <NFTCardList cards={cartas.sort((a,b) => {
            if (a.cant > b.cant) return -1;
            if (a.cant < b.cant) return 1;
            return 0;
          })}/>
          {/* {cartas.map((name, index) => <NFTCard key={index} cardName={name}/>)} */}
        </Accordion.Body>
      </Accordion.Item>);
  }

  if (!cargando && lucks && lucks.totalCartas > 0) {
    renderLucks = (<Accordion>
      <Accordion.Item eventKey="10">
        <Accordion.Body>
          <span>Total cards:</span> <Badge bg="secondary" style={{marginLeft: '10px'}}>{lucks.totalCartas}</Badge>
        </Accordion.Body>
      </Accordion.Item>
      {renderItem(0, lucks.canto, lucks.cartasCanto, "Basic Singing Lesson")}
      {renderItem(1, lucks.guitarra, lucks.cartasGuitarra, "Guitar Lesson")}
      {renderItem(2, lucks.orquesta, lucks.cartasOrquesta, "Orchestral Lesson")}
      {renderItem(3, lucks.produccion, lucks.cartasProduccion, "Production Lesson")}
      {renderItem(4, lucks.piano, lucks.cartasPiano, "Piano Lesson")}
      {renderItem(5, lucks.bateria, lucks.cartasDrum, "Drum Lesson")}
      {renderItem(6, lucks.carretera, lucks.cartasRoadCrew, "Road Crew Lesson")}
    </Accordion>);
  }

  return (<>
    <Container style={{paddingBottom: '60px'}}>
      <Row>
        <Col>
          <Form onSubmit={e => { e.preventDefault(); }}>
            <Form.Group className="mb-3" controlId="fieldGroup">
              <Form.Label>RisingStar Username</Form.Label>
              <Form.Control type="username" placeholder="Enter your username" value={username} onChange={changeUsername}/>
            </Form.Group>
            <Form.Group className="mb-3" controlId="buttonGroup">
              <Button variant="primary" type="submit" onClick={calculate}>Calculate</Button>
            </Form.Group>
          </Form>
        </Col>
      </Row>
      <Row>
        <Col>
          <div>{notIncludedCards.length > 0 ?
            (
            <Alert variant='warning'>
                These cards do not count because they are not in the <a target="_blank" rel="noreferrer" href="https://www.risingstargame.com/main.asp?tab=faq">FAQ</a>:
                <Stack direction="horizontal" gap={3}>
                  {notIncludedCards.map((card, i) =>
                  <div key={card._id}>
                    {i > 0 ? <div className="vr"></div> : null}
                    <a href={"https://www.risingstargame.com/images/cards/"+card+".png"} rel="noreferrer" style={{ color: '#50a14f' }}>{card}</a>
                  </div>
                )}
                </Stack>
            </Alert>
            // <span>Please, notify the developer to include this cards: {notIncludedCards}</span>
            )
          : null}</div>
        </Col>
      </Row>
      <Row>
        <Col>
          {cargando ? <div>Loading your cards...</div> : renderLucks}
        </Col>
      </Row>
      {/* <Row>
        <Col>
          {error}
        </Col>
      </Row> */}

    </Container>
  </>);
}

export default LuckInstruments;
