import { Form, Button, Container, Row, Col } from 'react-bootstrap';
import { useEffect, useState } from 'react';
import { getMarketData, getAllUserCards, login} from '../utils/RisingStarUtils';
// import _marketData from './marketData.json'
// import _userData from './userData.json'

const STORAGE_KEY_MARKETDATA = 'marketData';
const STORAGE_KEY_USERDATA = 'allUserCards';

const DEFAULT_PRICESYMBOL = "STARBITS";
const PRICESYMBOLS = ["BATTLE", "BEER", "BHZM", "CBM", "DEC", "LEN", "ONEUP", "PIZZA", "SIM", "STARBITS", "STARPRO", "SWAP.HIVE", "SWAP.WAX"];

// const TYPES_IGNORAR = ['fanboost', 'location', 'founder', 'runner', 'xpboost', 'skillboost', 'fuel', 'crafted', 'records', 'festival', 'fanclub', 'special', 'energyboost'];
const ALL_TYPES = [
    "fanboost",
    "skillboost",
    "instrument",
    "transport",
    "people",
    "crafted",
    "special",
    "records",
    "energyboost",
    "festival",
    "fuel",
    "location",
    "runner",
    "fanclub",
    "xpboost",
    "founder"
];

let storageMarketData = window.localStorage.getItem(STORAGE_KEY_MARKETDATA);
let storageAllUserCards = window.localStorage.getItem(STORAGE_KEY_USERDATA);
function MissingCardsMarket() {
    const [username, setUsername] = useState("");
    const [cargando, setCargando] = useState(false);
    const [priceSymbol, setPriceSymbol] = useState(DEFAULT_PRICESYMBOL);
    const [selectedTypeClass, setSelectedTypeClass] = useState('people');
    const [prices, setPrices] = useState(null);
    const [missingCards, setMissingCards] = useState(null);
    const [foundCards, setFoundCards] = useState(null);
    // const [searchStatus, setSearchStatus] = useState("Searching");

    const changePriceSymbol = (e) => {
        setPriceSymbol(e.target.value);
    }

    const changeSelectedTypeClass = (e) => {
        setSelectedTypeClass(e.target.value);
    }

    const changeUsername = (e) => {
        setUsername(e.target.value);
    }

    const getMissingCards = (userCards, marketData) => {
        // const missingCards = marketCardsSimplified
        //     .filter(value => userCardsSimplified.indexOf(value.split('|')[0] + "|" + value.split('|')[1]) === -1);
        const missingCards = marketData
            .filter((value, index, self) =>{
                const userCard = userCards.findIndex(card => card.properties.type === value.grouping.type && card.properties.class === value.grouping.class);
                const selfIndex = self.findIndex(e => e.grouping.type === value.grouping.type && e.grouping.class === value.grouping.class);
                // console.log('selfIndex(' + selfIndex + ')===index(' + index + ') | userCard=', userCard)
                return userCard === -1 && selfIndex === index;
            });
        // console.log('missingCards=', missingCards)
        return missingCards;
    }

    const findMissingCardsPrices = async (marketData, allUserCards) => {
        console.time('getMissingCards');
        const missingCards = getMissingCards(allUserCards, marketData);
        if (!missingCards || missingCards.length === 0) {
            console.log('invalid missingCards')
            return;
        }
        console.timeEnd('getMissingCards');
        setMissingCards(missingCards);

        console.time('prices');
        const prices = marketData.filter((value, index, self) => {
            const isMissing = missingCards.find(marketCard =>
                marketCard.grouping.type === value.grouping.type &&
                marketCard.grouping.class === value.grouping.class) !== null;
            return isMissing;
        })
        console.timeEnd('prices');
        setPrices(prices);
    }

    const _getAllUserCards = async () => {
        let allUserCards = null;
        console.time('allUserCards');
        allUserCards = await getAllUserCards(username);
        if (!allUserCards || allUserCards.length === 0) {
            console.log('invalid allUserCards')
            return;
        }
        console.timeEnd('allUserCards');
        return allUserCards;
    }

    const _getMarketData = async () => {
        let marketData = null;
        if (storageMarketData === null) {
            console.time('marketData');
            marketData = await getMarketData(username);
            if (!marketData || marketData.length === 0) {
                console.log('invalid marketData')
                return;
            }
            console.timeEnd('marketData');
            window.localStorage.setItem(STORAGE_KEY_MARKETDATA, JSON.stringify({
                fecha: new Date(),
                marketData
            }));
        } else {
            console.log('Sacando marketData del localStorage')
            marketData = JSON.parse(storageMarketData).marketData;
        }
        return marketData;
    }
    const find = () => {
        setCargando(true);
        // TODO: Mantener el login
        login(username, async (res) => {
            if (res.success) {
                await consolidate();
            } else { // ELSE, SOMETHING FAILED UPON LOGIN, PRINT ERROR TO CONSOLE
                console.error(res.message);
                alert("Login error " + res.message)
            }

            setCargando(false);
        });
    }

    const consolidate = async () => {
        if (username === '') {
            console.log('invalid username')
            return;
        }

        setCargando(true);
        const allUserCards = await _getAllUserCards();
        const marketData = await _getMarketData();

        console.time('findMissingCardsPrices');
        findMissingCardsPrices(marketData, allUserCards);
        console.timeEnd('findMissingCardsPrices');
        setCargando(false);
    }

    // Comparo MIS cartas con las cartas del market por lo cual, si no hay ofertas no tiene en cuenta la carta
    useEffect(e => {
        if (!missingCards || missingCards.length === 0 || !prices || prices.length === 0) {
            return;
        }
        console.time('_foundCards');
        const _foundCards = missingCards
            .filter(value => value.grouping.class === selectedTypeClass)
            .map((e,index) => {
                const name = e.grouping.type;
                const cardPrices = prices
                    .filter(c => c.priceSymbol === priceSymbol && c.grouping.type === name && c.grouping.class === selectedTypeClass)
                    .sort((a, b) => {
                        if (parseInt(a.price) > parseInt(b.price)) return 1;
                        if (parseInt(a.price) < parseInt(b.price)) return -1;
                        return 0;
                    });

                // console.log('name=' + name + ' | cardPrices: ', cardPrices);
                const card = cardPrices.length > 0 ? cardPrices[0] : null;
                const lowest = card ? card.price : null;
                const highest = cardPrices.length > 0 ? (cardPrices[cardPrices.length - 1].price) : null;
                return {
                    id: (card ? card._id : index),
                    name,
                    class: (card ? card.grouping.class : null),
                    lowest,
                    highest,
                    total: cardPrices.length
                };
            });
        console.timeEnd('_foundCards');
        // console.log('_foundCards=', _foundCards);
        setFoundCards(_foundCards);
    }, [missingCards, prices, selectedTypeClass, priceSymbol]);

    const renderFoundCards = () => {
        if (!foundCards || foundCards.length === 0) {
            return (<span>No offers available</span>);
        }

        return foundCards
            .sort((a, b) => {
                if (parseInt(a.lowest) > parseInt(b.lowest)) return 1;
                if (parseInt(a.lowest) < parseInt(b.lowest)) return -1;
                return 0;
            })
            .map(e => {
                const name = e.name;
                let number = name.indexOf(' ') > 0 ? name.substring(0, name.indexOf(' ')).toLowerCase() : name.toLowerCase();
                const extension = number.endsWith('a') ? ".gif" : ".png";

                const lowest = e.lowest;
                const highest = e.highest;

                return (<div key={e.id} style={{display:"flex", flexWrap:"wrap"}}>
                    <div className="rscard" data-toggle="modal" data-target="#nftDetails">
                        <div className="card-image">
                            <img src={"https://www.risingstargame.com/images/cards/" + name + extension} alt={e.id} className="img-fluid gray-image card-img" />
                        </div>
                    </div>
                    <div className="price-data" style={{display: "flex", flexDirection: "column", alignSelf: "center"}}>
                        <div>{name}</div>
                        {/* <div>(Class: {e.class})</div> */}
                        {e.total === 0 ? <>
                                <div>No Offers available</div>
                            </>
                        : <>
                            {/* <div>Ver en el market de RisingStar: <a href="https://www.risingstargame.com/main.asp?tab=marketplace&order=price_asc"></div> */}
                            <div>Offers: {e.total}</div>
                            <div>Lowest Price: {lowest} {priceSymbol}</div>
                            <div>Highest Price: {highest} {priceSymbol}</div>
                        </>}
                    </div>
                </div>);
            })
    }

    const renderStoreMarketCardsInfo = () => {
        if (storageMarketData === null)
            return null;
        const storageDate = new Date(JSON.parse(storageMarketData).fecha);
        var diffMs = (new Date() - storageDate); // milliseconds between now & Christmas
        // var diffDays = Math.floor(diffMs / 86400000); // days
        // var diffHrs = Math.floor((diffMs % 86400000) / 3600000); // hours
        var diffMins = Math.round(((diffMs % 86400000) % 3600000) / 60000);

        const minutesToUpdate = 10;
        function update() {
            window.localStorage.removeItem(STORAGE_KEY_MARKETDATA);
            storageMarketData = null;
            _getMarketData();
        }
        return (<div style={{display: 'flex', alignContent:'center'}}>
            <div style={{marginRight:'10px'}}>Market data storage date: {storageDate.toLocaleString()}</div>
            {diffMins >= minutesToUpdate ? <div><input type="button" onClick={update} value="Update"/></div> : <div>Wait {minutesToUpdate} minutes to update</div>}
        </div>);
    }

    const renderStoreInfo = () => {
        return (<>
            <div>Advice: For performance reasons market data is cached on browser storage and can be updated after some time.</div>
            {/* {renderStoreUserCardsInfo()} */}
            {renderStoreMarketCardsInfo()}
        </>);
    }

    return (<>
        <Container style={{paddingBottom: '60px'}}>
            <Row>
                {renderStoreInfo()}
            </Row>
            <Row>
                <Col>
                <Form.Group className="mb-3" controlId="fieldGroup">
                    <Form.Label>RisingStar Username</Form.Label>
                    <Form.Control type="username" placeholder="Enter your username" value={username} onChange={changeUsername}/>
                </Form.Group>
                <Form.Group className="mb-3" controlId="fieldGroup">
                    <Form.Label>Price symbol</Form.Label>
                    <Form.Select aria-label="Select the price symbol" value={priceSymbol} onChange={changePriceSymbol}>
                        {PRICESYMBOLS.map(e=><option key={e} value={e}>{e}</option>)}
                    </Form.Select>
                </Form.Group>
                <Form.Group className="mb-3" controlId="fieldGroup">
                    <Form.Label>Collection class:</Form.Label>
                    {/* <Form.Control type="priceSymbol" placeholder="Enter the price symbol" value={priceSymbol} onChange={changePriceSymbol}/> */}
                    <Form.Select aria-label="Select the collection class" value={selectedTypeClass} onChange={changeSelectedTypeClass}>
                        {ALL_TYPES.map(e=><option key={e} value={e}>{e.toUpperCase()}</option>)}
                    </Form.Select>
                </Form.Group>
                <Form onSubmit={e => { e.preventDefault(); }}>
                    <Form.Group className="mb-3" controlId="buttonGroup">
                        <Button variant="primary" type="submit" onClick={find}>Find</Button>
                    </Form.Group>
                </Form>
                </Col>
            </Row>
            <Row>
                <Col>
                <div>
                    {cargando ?
                    <span>Searching ...</span>
                    :
                    foundCards !== null ?
                        renderFoundCards()
                    :
                        <span>Select collection class and price symbol and click find</span>
                    }
                </div>
                </Col>
            </Row>
        </Container>
    </>);
}

export default MissingCardsMarket;
