const getRequestBody = (body, query, page) => {
    const res = JSON.parse(JSON.stringify(body));
    res.id = page + 1;
    res.params.offset = page * res.params.limit;
    res.params.query = query;
    return res;
}

const getPage = async (body, query, page) => {
    const _body = getRequestBody(body, query, page);

    const res = await fetch("https://custr.de.ryamer.com/contracts", {
        "headers": {
            "accept": "application/json, text/plain, */*",
            "accept-encoding": "gzip, deflate, br",
            "accept-language": "es-US,es-419;q=0.9,es;q=0.8,en;q=0.7",
            "content-type": "application/json",
            "access-control-allow-origin": "*",
            "origin": "https://www.risingstargame.com",
            "referer": "https://www.risingstargame.com",
            "user-agent": "Mozilla/5.0 (Windows NT 10.0; Win64; x64) AppleWebKit/537.36 (KHTML, like Gecko) Chrome/99.0.4844.51 Safari/537.36"
        },
        "body": JSON.stringify(_body),
        "method": "POST"
    }).then(res => res.json());

    return res.result;
}

const collectMarketData = async (page) => {
    let todomarket = [];
    while (page >= 0) {
        const parciales = await getPage(REQUEST_BODY_MARKET, {}, page);
        if (parciales && parciales.length > 0) {
            todomarket.push(...parciales);
            ++page;
        } else {
            page = -1;
        }
    }
    todomarket.sort((a, b) => {
        if (parseInt(a.price) > parseInt(b.price)) return 1;
        if (parseInt(a.price) < parseInt(b.price)) return -1;
        return 0;
    });
    return todomarket;
}

export const getMarketData = async () => {
    return await collectMarketData(0);
}
const fetchData = async (username, page) => {
    let allUserCards = [];
    while (page >= 0) {
        const parciales = await getPage(REQUEST_BODY_USERCARDS, {"account":username}, page);
        if (parciales && parciales.length > 0) {
            allUserCards.push(...parciales);
            ++page;
        } else {
            page = -1;
        }
    }
    allUserCards.sort((a, b) => {
        if (parseInt(a.price) > parseInt(b.price)) return 1;
        if (parseInt(a.price) < parseInt(b.price)) return -1;
        return 0;
    });
    return allUserCards;
}

