export const loadAllCards = async (username, offset, cards) => {
    // const allCards = await fetchDataSSC(username, offset).then((data) => {
    const allCards = await fetchData(username, offset).then((data) => {
        if (data !== null && data.length > 0) {
            cards = [...cards, ...data];
            return loadAllCards(username, offset + data.length, cards);
        } else {
            return cards;
        }
    });
    return allCards;
}

export const loadInstrumentCards = async (username) => {
    const cardsData = await loadAllCards(username, 0, []);
    //filtro solo cards de las lecciones
    const filteredCards = cardsData.filter(e => e.properties.class === "instrument" || e.properties.class === "transport" || e.properties.class === "crafted");
    return filteredCards;
}

export const getAllUserCards = async (username) => {
    let cardsData = await loadAllCards(username, 0, []);
    return cardsData;
}

export const parseStats = function (stats) {
    //"stats": "100,1,75,3"
    //stat=fans,luck,skill,IM
    const statsSplit = stats.split(',');
    return {
        fans: statsSplit[0],
        luck: statsSplit[1],
        skill: statsSplit[2],
        IM: statsSplit[3]
    };
}

export const login = async (username, callback) => {
    if (window.hive_keychain) {
        window.hive_keychain.requestHandshake(() => {}) //Keychain Found!
    } else {
        alert("Hive KeyChain needed to login");
        return {
            message: "Hive KeyChain needed to login"
        };
    }

    window.hive_keychain.requestSignBuffer(username.toLowerCase(), 'RS Tools Login (DEV)', 'Posting', async function (response, err) {
        callback(response);
    });
}
